.not-found-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
  text-align: center;
}

.not-found-content {
  max-width: 500px;
  padding: 40px;
}

.not-found-content h1 {
  font-size: 120px;
  margin: 0;
  color: #667eea;
  font-weight: 700;
}

.not-found-content h2 {
  font-size: 32px;
  margin: 10px 0;
  color: #2c3e50;
}

.not-found-content p {
  font-size: 18px;
  color: #7f8c8d;
  margin-bottom: 30px;
}

.home-link {
  display: inline-block;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  text-decoration: none;
  padding: 12px 30px;
  border-radius: 8px;
  font-weight: 600;
  transition: transform 0.2s ease;
}

.home-link:hover {
  transform: translateY(-2px);
}